





















































































.round-fg {
    height: 50vh;
    max-width: 800px;
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    grid-template-areas: "message form";
    grid-column-gap: 1rem;
}

.q-form {
    grid-area: form;
    background: rgba(240, 240, 240, 0.35);
    border-radius: 10px;
}

.message {
    grid-area: message;
    position: relative;
    color: #fff;
}

.cms-icon {
    font-size: 15rem;
}

@media (max-width: 800px) {
    .message {
        display: none;
    }
    .round-fg {
        grid-template-areas: "form";
        grid-template-columns: 1fr;
        width: 75%;
    }
}
